<template>
	<view>
		<cu-custom bgColor="theme-bg-color" :isBack="true">
			<block slot="backText">返回</block>
			<block slot="content">修改个人信息</block>
		</cu-custom>
		<form>
			<input type="text" style="opacity: 0;" name="imageUrls" v-model="formData.imageUrls"></input>

		<!-- 	<view class="cu-form-group">
				<view class="title">名称</view>
				<input placeholder="请输入名称" name="name" v-model="formData.name"></input>
			</view> -->
			<view class="cu-form-group">
				<view class="title">密码</view>
				<input placeholder="请输入密码" name="name" v-model="formData.password"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">邮箱</view>
				<input placeholder="请输入邮箱" name="email" v-model="formData.email"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">手机号码</view>
				<input placeholder="请输入手机号码" name="input" v-model="formData.phone"></input>
				<view class="cu-capsule radius">
					<view class='cu-tag bg-blue '>
						+86
					</view>
					<view class="cu-tag line-blue">
						中国大陆
					</view>
				</view>
			</view>
			<view class="cu-form-group">
				<view class="title">个人地址</view>
				<input placeholder="请输入个人地址" name="input" v-model="formData.residentialAddress"></input>
			</view>

		
			<view class="cu-bar bg-white ">
				<view class="action">
					图片上传
				</view>
				<view class="action">
					{{imgList.length}}/1
				</view>
			</view>
			<view class="cu-form-group">
				<view class="grid col-4 grid-square flex-sub">
					<view class="bg-img" v-for="(item,index) in imgList" :key="index" @tap="ViewImage"
						:data-url="imgList[index]">
						<image :src="imgList[index]" mode="aspectFill"></image>
						<view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
							<text class='cuIcon-close'></text>
						</view>
					</view>
					<view class="solids" @tap="ChooseImage" v-if="imgList.length<4">
						<text class='cuIcon-cameraadd'></text>
					</view>
				</view>
			</view>
		
			<view class="padding margin-top">

				<button class="cu-btn block theme-bg-color margin-tb-sm lg" @click="Save(0)">保存</button>




			</view>
		</form>
	</view>
</template>

<script>
	import store from "@/store";
	import {
		mapGetters
	} from "vuex";
	export default {
		data() {
			return {
				typeItem: [],
				id: "",
				typeIndex: -1,
				TabCur: 0,
				keyword: "",
				imgList: [],
				formData: {
					name: "",
					"address": "",
					"phoneNumber": "",
					"description": "",
					"imageUrls": "",
					"email": "",
					"name": "",
					"height": "",
					"weight": ""
				}
			}
		},
		computed: {
			...mapGetters(["userInfo"]),
		},
		async onLoad() {
			if (this.$comm.CheckToken()) {

				await this.GetUserInfo();

			}
		},
		async onShow() {


		},
		methods: {

			async GetUserInfo() {

				this.formData = this.userInfo;
				if (this.userInfo.avatar) {
					this.imgList = [this.userInfo.avatar];
				}

			},
			ViewImage(e) {
				uni.previewImage({
					urls: this.imgList,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				uni.showModal({
					title: '朋友',
					content: '确定要删除此头像吗？',
					cancelText: '再看看',
					confirmText: '再见',
					success: res => {
						if (res.confirm) {
							this.imgList.splice(e.currentTarget.dataset.index, 1)
						}
					}
				})
			},
			async ChooseImage() {
				var {
					tempFilePaths,
					tempFiles
				} = await this.$comm.ChooseImageAsync(1);
			
				if (tempFilePaths && tempFilePaths.length > 0) {
					let {
						data
					} = await this.$http.Upload(tempFilePaths[0]);
					
					console.log("data",data);
					this.imgList.push(data);
					
				}
			},
			async Save(type) {

				this.formData.avatar = this.imgList[0];

				
				if (!this.formData.password) {
					await this.$comm.ShowToast("密码不能为空");
					return
				}
				if (!this.formData.phone) {
					await this.$comm.ShowToast("联系方式不能为空");
					return
				}
				if (!this.formData.avatar) {
					await this.$comm.ShowToast("图片不能为空");
					return
				}
			
				await this.$http.PostReturnPage("/user/update", this.formData);

			},
		


		}
	}
</script>

<style scoped>
	.publish {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #1CBBB4;
		width: 100upx;
		height: 100upx;
		border-radius: 50%;
		position: fixed;
		right: 30upx;
		bottom: 150upx;
	}
</style>
